import { v4 as uuidv4 } from "uuid";
import './resume.css';
import ppic from './ppic.png';
import jsPDF from 'jspdf';

function Resume({profileData, educationData, skillsData, miniProjectData, socialMentionData}) {

    const generatePdf = () => {
        let doc = new jsPDF(
            {
                orientation: 'p',
                unit: 'px',
                format: 'a4',
                hotfixes: ["px_scaling"],
                putOnlyUsedFonts: true,
            },
        );

        doc.html(
            document.querySelector("#resume-div"), 
            {
                callback: function (pdf) {
                    pdf.save("generated");
                },
                margin: [50,50,50,50]
            }
        );
    }

    return(
        <div>
            <div className="resume" id="resume-div">
                <section className="profile-section">
                    {/* <img src={ppic} alt="profile" /> */}
                    <h1>{"Juggu"} {"Jones"}</h1>
                    <p><b>Phone no: </b>{"5555555555"}</p>
                    <p className="address">{"pulathota, hafizpet, Hyderabad, Telangana."}</p>
                </section>
                
                <section className="education-section">
                    <h1>Educational Qualifications</h1>
                    <ul>
                         
                        <li key={uuidv4()}>
                            <p><b>{"10th"}</b> &mdash; 
                            completed {"10th"} from <b>{"Narayana em high school"} </b>
                            in {2010} with the percentage of {70}.</p>
                        </li>

                        <li key={uuidv4()}>
                            <p><b>{"Inter"}</b> &mdash; 
                            completed {"Inter"} from <b>{"Narayana junior college"} </b>
                            in {2012} with the percentage of {76}.</p>
                        </li>

                        <li key={uuidv4()}>
                            <p><b>{"Degree"}</b> &mdash; 
                            completed {"Degree"} from <b>{"Narayana engineering college"} </b>
                            in {2045} with the percentage of {80}.</p>
                        </li>
                        
                    </ul>
                </section>
                
                <section className="skills-section">
                    <h1>Skills</h1>
                    <ul>
                        
                        <li key={uuidv4()}>
                            {"html"}
                        </li>

                        <li key={uuidv4()}>
                            {"css"}
                        </li>
                        
                        <li key={uuidv4()}>
                            {"javscript"}
                        </li>

                        <li key={uuidv4()}>
                            {"react js"}
                        </li>
                         
                    </ul>
                </section>
                
                <section className="projects-section">
                <h1>Projects</h1>
                    <ul>
                        
                        <li key={uuidv4()}>
                            <div className="project">
                                <h2>{"IOT"}</h2>
                                <p>
                                    <b>Tech Stack: </b>
                                    iot board
                                </p>
                                <p>
                                    
                                    <b>Description: </b>:
                                    
                                    The Iot project is done by using iot board and using backend software to control the board.
                                </p>
                            </div>
                        </li>
                    </ul>
                </section>

                <section className="social-links">
                    <h1>My online presence</h1>
                    <ul>
                        
                        <li key={uuidv4()}>
                            <div className="social">
                                <h3>
                                    Github
                                </h3>
                                <p>
                                  https://www.github.com/repositories/group/ddhdhd/ddhd  
                                </p>
                            </div>
                        </li>

                        <li key={uuidv4()}>
                            <div className="social">
                                <h3>
                                    Linkedin
                                </h3>
                                <p>
                                  https://www.github.com/repositories/group/ddhdhd/ddhd  
                                </p>
                            </div>
                        </li>

                        <li key={uuidv4()}>
                            <div className="social">
                                <h3>
                                    Hackerrank
                                </h3>
                                <p>
                                  https://www.github.com/repositories/group/ddhdhd/ddhd  
                                </p>
                            </div>
                        </li>
                    </ul>
                </section>
                
            </div>
            
            <div>
                <button type="button" onClick={generatePdf}>
                    Download
                </button>
            </div>
            
        </div>
    )
}


export default Resume;